import { getStudyInfo, editStudyInfo } from './api.js'

// –§—É–Ω–∫—Ü–∏—è –¥–ª—è –ø–æ–ª—É—á–µ–Ω–∏—è URL-–ø–∞—Ä–∞–º–µ—Ç—Ä–∞ –∏–∑ —Å—Ç—Ä–æ–∫–∏
function getUrlParameter(name) {
    const urlParams = new URLSearchParams(window.location.search);
    return urlParams.get(name);
}

// –ü–æ–ø—ã–∫–∞ –ø–æ–ª—É—á–µ–Ω–∏—è –ø–∞—Ä–∞–º–µ—Ç—Ä–∞ 'study_id'
const studyIdValue = getUrlParameter('study_id');

async function showPopUp(popupBack) {
    if (studyIdValue) {
        try {
            // –ü–æ–∫–∞–∑—ã–≤–∞–µ–º –ø–æ–ø-–∞–ø
            popupBack.style.display = 'flex';

            // –ü–æ–ª—É—á–∞–µ–º –¥–∞–Ω–Ω—ã–µ –¥–ª—è –ø–æ–ø-–∞–ø–∞
            const study_data = await getStudyInfo(studyIdValue);

            // –ó–∞–ø–æ–ª–Ω—è–µ–º –ø–æ–ø-–∞–ø
            let studyNameField = document.getElementById('study_name');
            studyNameField.value = study_data.study_name; 
            let studyDescField = document.getElementById('study_description');
            studyDescField.value = study_data.study_description; 

            // –õ–æ–≥–∏–∫–∞ –∫–Ω–æ–ø–∫–∏ "–°–æ—Ö—Ä–∞–Ω–∏—Ç—å" –≤ –ø–æ–ø-–∞–ø–µ
            const saveButton = document.getElementById('popup_button_1');
            saveButton.addEventListener('click', function() {
                // –û–±–Ω–æ–≤–ª—è–µ–º –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—é –Ω–∞ —Å–µ—Ä–≤–µ—Ä–µ
                popupBack.style.display = 'none';
                let data_update = {
                    "study_name": studyNameField.value,
                    "study_description": studyDescField.value
                }
                editStudyInfo(studyIdValue, data_update)

                // –û–±–Ω–æ–≤–ª—è–µ–º –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—é –≤ –∏–Ω—Ç–µ—Ä—Ñ–µ–π—Å–µ
                let component = document.getElementById('default_info');
                fulfillMainPage(component)
            });

            // –õ–æ–≥–∏–∫–∞ –∫–Ω–æ–ø–∫–∏ "–û—Ç–º–µ–Ω–∏—Ç—å" –≤ –ø–æ–ø-–∞–ø–µ
            const cancelButton = document.getElementById('popup_button_2');
            cancelButton.addEventListener('click', function() {
                popupBack.style.display = 'none';
    });
        } catch (error) {
            // –°–∫—Ä—ã–≤–∞–µ–º –ø–æ–ø-–∞–ø
            popupBack.style.display = 'none';

            // –£–≤–µ–¥–æ–º–ª—è–µ–º –æ–± –æ—à–∏–±–∫–µ
            console.log('Error fetching study data:', error);
            alert("–û—à–∏–±–∫–∞! –ò—Å—Å–ª–µ–¥–æ–≤–∞–Ω–∏–µ –Ω–µ–ª—å–∑—è —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å.")
        }
    }
}

// –§—É–Ω–∫–∫—Ü–∏—è –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è —Å—Ç—Ä–∞–Ω–∏—Ü—ã –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–µ–π –æ–± –∏—Å—Å–ª–µ–¥–æ–≤–∞–Ω–∏–∏
async function fulfillMainPage(component) {

    if (studyIdValue) {
        try {
            const study_data = await getStudyInfo(studyIdValue);
            component.innerHTML = `
                <span class="headline_1">–ì–ª–∞–≤–Ω–æ–µ</span>

                <div class="content_section">
                    <span class="headline_2">–û–± –∏—Å—Å–ª–µ–¥–æ–≤–∞–Ω–∏–∏</span>
                    <span class="content light secondary">
                        –ù–∞–∑–≤–∞–Ω–∏–µ......................<span class="content primary">${study_data.study_name}</span>
                    </span>
                    <span class="content light secondary">
                        –û–ø–∏—Å–∞–Ω–∏–µ......................<span class="content primary">${study_data.study_description}</span>
                    </span>
                    <span class="content light secondary">
                        –¢–∏–ø.................................<span class="content primary">${study_data.study_type}</span>
                    </span>
                </div>

                <div class="content_section">
                    <span class="headline_2">–ü—Ä–æ–≥—Ä–µ—Å—Å</span>
                    <span class="content light secondary">
                        –°—Ç–∞—Ç—É—Å...........................<span class="content primary active_text">${study_data.study_status}</span>
                    </span>
                    <span class="content light secondary">
                        –≠—Ç–∞–ø —Å–±–æ—Ä–∞....................<span class="content primary">${study_data.study_stage}</span>
                    </span>
                    <span class="content light secondary">
                        –£—á–∞—Å—Ç–Ω–∏–∫–∏......................<span class="content primary">${study_data.study_participants}</span>
                    </span>
                    <span class="content light secondary">
                        CR < 80%.......................<span class="content primary">${study_data.study_cr}</span>
                    </span>
                    <span class="content light secondary">
                        –°—Ä–µ–¥–Ω–∏–π CR...................<span class="content primary">${study_data.study_avg_cr}</span>
                    </span>
                </div>
            `;
        } catch (error) {
            console.log('Error fetching study data:', error);
            component.innerHTML = `
                <div class="content_section">
                    <span class="headline_1">–û—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ –¥–∞–Ω–Ω—ã—Ö!</span>
                    <span class="content light secondary">
                        –ü–µ—Ä–µ–¥–∞–Ω–Ω—ã–π –ø–∞—Ä–∞–º–µ—Ç—Ä study_id –Ω–µ –≤–∞–ª–∏–¥–µ–Ω. –ü–æ–ø—Ä–æ–±—É–π—Ç–µ "pr215".
                    </span>
                </div>`;
        }
    } else {
        component.innerHTML = `
            <div class="content_section">
                <span class="headline_1">–û—à–∏–±–∫–∞!</span>
                <span class="content light secondary">
                    –ü–∞—Ä–∞–º–µ—Ç—Ä study_id –Ω–µ —É–∫–∞–∑–∞–Ω. –ü—Ä–∏–º–µ—Ä –≤–∞–ª–∏–¥–Ω–æ–≥–æ study_id ‚Äì pr215.
                </span>
            </div>
        `;
    }
}

// –ü–æ–¥–≥—Ä—É–∑–∫–∞ –ª–æ–≥–∏–∫–∏ –ø–æ—Å–ª–µ –∑–∞–≥—Ä–∑–∫–∏ —Å—Ç—Ä–∞–Ω–∏—Ü—ã
window.addEventListener('DOMContentLoaded', () => {

    // –î–æ–±–∞–≤–ª–µ–Ω–∏–µ –±–ª–æ–∫–∞ —Å –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–µ–π –ø—Ä–æ –∑–∞–≥—Ä—É–∑–∫—É
    const component = document.createElement('div');
    component.id = "default_info";
    component.className = "page_content";
    component.innerHTML = `
    <div class="content_section">
        <span class="headline_1">–ó–∞–≥—Ä—É–∑–∫–∞ –¥–∞–Ω–Ω—ã—Ö</span>
        <span class="content light secondary">
            –ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –ø–æ–¥–æ–∂–¥–∏—Ç–µ, –¥–∞–Ω–Ω—ã–µ –∑–∞–≥—Ä—É–∂–∞—é—Ç—Å—è...
        </span>
    </div>`;
    const main_container = document.getElementById("uc_1");
    main_container.appendChild(component);

    // –õ–æ–≥–∏–∫–∞ –∫–Ω–æ–ø–∫–∏ "–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å" –≤ –ø–æ–ø-–∞–ø–µ
    const editButton = document.getElementById('edit_button');
    const popupBack = document.querySelector('.popup_back');
    editButton.addEventListener('click', function() {
        showPopUp(popupBack)
    });

    // –ó–∞–ø–æ–ª–Ω–µ–Ω–∏–µ —Å—Ç—Ä–∞–Ω–∏—Ü—ã –¥–∞–Ω–Ω—ã–º–∏ —Å API
    fulfillMainPage(component);
});
